export function themeToggler() {
  const body = document.querySelector('body');
  body.classList.toggle('body_dark');

  const title = document.querySelector('.main-title');
  title.classList.toggle('main-title_dark');

  const results = document.querySelector('.results');
  results.classList.toggle('results_dark');

  const SwitherName = document.querySelectorAll('.swither-name');
  SwitherName[0].classList.toggle('swither-name_dark');
  SwitherName[1].classList.toggle('swither-name_dark');

  const SwitherSpan = document.querySelector('.swither__span');
  SwitherSpan.classList.toggle('swither__span_dark');

  const level = document.querySelectorAll('.level');
  level.forEach((item) => {
    item.classList.toggle('level_dark');
  });

  const custom = document.querySelector('.custom-settings');
  if (custom && !custom.classList.contains('hidden')) {
    const settingsItem = document.querySelectorAll('.settings-item');

    settingsItem.forEach((item) => {
      item.classList.toggle('settings-item_dark');
      if (item.children[0]) {
        item.children[0].classList.toggle('settings-item__name_dark');
      }
    });
  }


  let targetClasses = ['.cell_closed',
    '.cell-mined_closed',
    '.cell-one_closed',
    '.cell-two_closed',
    '.cell-three_closed',
    '.cell-four_closed',
    '.cell-five_closed',
    '.cell-six_closed',
    '.cell-seven_closed',
    '.cell-eight_closed',
    '.game__header-container',
    '.game__footer-container',
    '.smile_unpressed',
    '.cell_opened',
    '.cell-one_opened',
    '.cell-two_opened',
    '.cell-three_opened',
    '.cell-four_opened',
    '.cell-five_opened',
    '.cell-six_opened',
    '.cell-seven_opened',
    '.cell-eight_opened',
    '.cell-mined_opened-loss',
    '.cell-mined_opened'


  ]
  for (let i = 0; i < targetClasses.length; i += 1) {
    const cellsClosed = document.querySelectorAll(targetClasses[i])
    cellsClosed.forEach(item => {
      item.classList.toggle('dark')
    })
  }


}
