import { LocalStorageActions } from './functions';

const localStor = new LocalStorageActions();
const activeLevel = localStor.getItem('active-level');
// eslint-disable-next-line consistent-return
function getMines() {

  if (activeLevel) {
    if (!localStor.getItem(`${activeLevel.toLowerCase()}-mines`)) {
      return 10;
    }
    if (localStor.getItem(`${activeLevel.toLowerCase()}-mines`)) {

      return Number(localStor.getItem(`${activeLevel.toLowerCase()}-mines`));
    }
  } else {
    return 10;
  }

}


export const options = {
  Beginner: {
    width: 10,
    height: 10,
    mines: getMines(),
  },
  Intermediate: {
    width: 15,
    height: 15,
    mines: getMines(),
  },
  Expert: {
    width: 25,
    height: 25,
    mines: getMines(),
  },
};

function getWidthSetting() {
  return Number(localStor.getItem('custom-width'));
}
function getHeightSetting() {
  return Number(localStor.getItem('custom-height'));
}

export class GetLevelOptions {
  constructor(level) {
    if (level !== 'Custom') {
      this.width = options[level].width;
      this.height = options[level].height;
      this.mines = options[level].mines;
    } else {
      this.width = getWidthSetting();
      this.height = getHeightSetting();
      this.mines = getMines();
    }
  }

  getWidth() {
    return this.width;
  }

  getHeight() {
    return this.height;
  }

  getMines() {
    return this.mines;
  }

  getGameMaxWidth() {
    return ((this.width * 24) + (18 * 2));
  }

  getWidthPixels() {
    return (this.width * 24);
  }

  getHeightPixels() {
    return (this.height * 24);
  }
}
